.input {
    width: 250px;
    height: 40px;
    font-size: 1.8rem;
    text-align: center;
    color: #fff;

    background-color: rgb(24, 23, 23);
    border: none;
    outline: none;
}

.input::placeholder {
    color: #fff;
}

.input:focus {
    outline: 1px solid rgb(17, 236, 9);
}